import styles from '../../css/InfoBreakfast.module.css'
import CarouselDefault from "../UI/Carousel";
import {InfoBreakfastGallery} from '../../media/images/gallery/colazioni/InfoBreakfast'


const InfoBreakfast = (props) => {
    return <>
        <h2 className={styles.breakfast}>Le colazioni</h2>
        <div className={styles.container}>
            <div className={styles.textContainer}>
                <h3 className={styles.title}>4 colazioni per ogni gusto</h3>
                <p className={styles.text}>Tutte le colazioni comprendono caffe', 10 diversi tipi di the, succhi di frutta e marmellate</p>
                <p>Eventuali richieste speciali per la colazione devono essere richieste tramite la <a href="/">form</a> almeno 24 ore prima</p>
            </div>
            <CarouselDefault gallery={InfoBreakfastGallery} />
        </div>
            
        
    </>
}

export default InfoBreakfast;