import styles from "../../css/InfoRestaurant.module.css";
import { useEffect, useState } from "react";

const InfoFeature = () => {
  const [data, setData] = useState(null); // State variable to hold fetched data

  useEffect(() => {
    const fetchDataFromFirebase = async () => {
      try {
        const firebaseUrl = `https://bbdesign-415904-default-rtdb.europe-west1.firebasedatabase.app/info.json`;
        const response = await fetch(firebaseUrl);

        if (!response.ok) {
          throw new Error('Network response was not ok. Status: ' + response.status);
        }

        const jsonData = await response.json();
        setData(jsonData); // Set the fetched data into state
      } catch (error) {
        console.error('Error fetching data:', error);
      }
    };

    fetchDataFromFirebase(); // Fetch data when the component mounts
  }, []);

  // If data is null or not an object, return null or an error message
  if (!data || typeof data !== 'object') {
    return <p>Error: Unable to fetch data</p>; // Return an error message
  }

  // Get the keys of the object
  const keys = Object.keys(data);

  // Render the JSX with the fetched data
  return (
    <div className={styles.container}>
      <div>
        <div className={styles.flex}>
          {keys.map((key, index) => ( // Iterate over the keys of the object
            <div key={index}>
              <h3 className={styles.title}>{key}</h3>
              {data[key].map((item, subIndex) => ( // Map over the array associated with each key
                <div key={subIndex} className={styles.column}>
                  <p>{item}</p>
                </div>
              ))}
            </div>
          ))}
        </div>
        <p>Scarica il pdf con tutte le informazioni <a href="/">qui</a></p>
      </div>
    </div>
  );
};

export default InfoFeature;
