import Header from "../components/utils/Header";
import banner from '../media/banner/rooms.jpg';
import Nav from "../components/utils/Nav";
import styles from '../css/Rooms.module.css';
import { CarouselRoomOne } from "../components/room/CarouselRoomOne";
import { CarouselRoomTwo } from "../components/room/CarouselRoomTwo";
import Gallery from "../components/UI/Gallery";
import Footer from "../components/utils/Footer";
import GoToBooking from "../components/utils/GoToBooking";

const Rooms = () => {

    return <>
        <Nav />
        <Header title="Le Camere" img={banner} />
        <p className={styles.subtitle}>2 bellissime camere suite per singoli, coppie o famiglie fino a 4 persone</p>
        <h2 className={styles.title}>Sliding Door</h2>
        <div className={styles.container}>
            <Gallery images={CarouselRoomOne} text="Testo Camera 1" />
        </div>
        <GoToBooking img={banner} />
        <h2 className={styles.title}>Junior Suite</h2>
        <div className={styles.container}>
            <Gallery images={CarouselRoomTwo} text="Testo Camera 2" />
        </div>
        <Footer />
    </>

}

export default Rooms;