import Footer from "../components/utils/Footer";
import Header from "../components/utils/Header";
import Nav from "../components/utils/Nav";
import InfoHouse from "../components/home/InfoHouse";
import InfoBreakfast from "../components/home/InfoBreakfast";
import banner from '../media/banner/home.jpg';
import GoToBooking from "../components/utils/GoToBooking";
import styles from '../css/Home.module.css';
import InfoShooting from "../components/home/InfoShooting";

const LandingPage = () => {

    return <>
        <Nav />
        <Header title="Recycling Art Loft" img={banner} />
        <div className={styles.home}>
            <div>
                <p className={styles.subheading}>Libri Da Sfogliare, Vinili da ascoltare, oggetti&nbsp;d'arte e tante altre amenit</p>
            </div>
            <div></div>
        </div>
        <InfoBreakfast />
        <InfoHouse />
        <GoToBooking img={banner} />
        <InfoShooting />
        <Footer />
    </>
}

export default LandingPage;