import logo from '../../media/logo-bbdesign.png';
import { Link } from 'react-router-dom';
import styles from '../../css/Nav.module.css';
import { isBrowser, isMobile } from 'react-device-detect';
import Mobile from './Mobile';
const Nav = () => {
    if(isBrowser) {
        return <div className={styles.sticky}>
        <nav className={styles.nav}>
        <li className={styles.link}><Link to="/booking">Prenota</Link></li>
        <li className={styles.link}><Link to="/rooms">Camere</Link></li>
        <li className={styles.link}><Link to="/home"><img src={logo} alt='logo of bnb design bologna' className={styles.logo}/></Link></li>
        <li className={styles.link}><Link to="/info">Info</Link></li>
        <li className={styles.link}><div><span>IT</span><span>EN</span></div></li>
        </nav>
        
        </div>
    }

    if(isMobile) {
        return <>
        <Mobile />
        </>

    }


}

export default Nav;