import styles from '../../css/InfoLocation.module.css';
import Collapsible from 'react-collapsible';
import { useState } from 'react';
const InfoLocation = () => {
    const [countFirst, setCountFirst] = useState(0);
    const [countSecond, setCountSecond] = useState(0);
    

    return <>
        <h2 className={styles.title}>Locazione e Mobilit</h2>
        <div className={styles.container}>
            <div>
                <ul>
                    <li><h3>Come raggiungerci</h3></li>
                    <li>
                        <Collapsible 
                            trigger="People Mover +" 
                            triggerWhenOpen="People Mover -" 
                            transitionTime="800" open={countFirst === 1} onTriggerOpening ={() => setCountFirst(1)}>
                        <p>Descrizione People Mover</p>
                        </Collapsible>
                    </li>
                    <li>
                        <Collapsible 
                            trigger="Autobus +" 
                            triggerWhenOpen="Autobus -" 
                            transitionTime="800" open={countFirst === 2} onTriggerOpening ={() => setCountFirst(2)}>
                        <p>Autobus 27 fermata Aldini Valeriani</p>
                        </Collapsible>
                    </li>
                    <li>
                        <Collapsible 
                            trigger="Treno +" 
                            triggerWhenOpen="Treno -" 
                            transitionTime="800" open={countFirst === 3} onTriggerOpening={() => setCountFirst(3)}>
                        <p>Treno Centrale</p>
                        </Collapsible>
                    </li>
                </ul>
                </div>
                <div>
                <ul>
                    <li><h3>Come Spostarsi a Bologna</h3></li>
                    <li>
                        <Collapsible 
                            trigger="Bici/Mobike +" 
                            triggerWhenOpen="Bici/Mobike -" 
                            transitionTime="800" open={countSecond === 1} onTriggerOpening={() => setCountSecond(1)}>
                        <p>Come spostarsi in bici e app Mobike</p>
                        </Collapsible>
                    </li>
                    <li>
                        <Collapsible 
                            trigger="Car Sharing +" 
                            triggerWhenOpen="Car Sharing -" 
                            transitionTime="800" open={countSecond === 2} onTriggerOpening={() => setCountSecond(2)}>
                        <p>Scarica l'App Corrente e Enjoy</p>
                        </Collapsible>
                    </li>
                    <li>
                        <Collapsible 
                            trigger="Scooter Sharing +" 
                            triggerWhenOpen="Scooter Sharing -" 
                            transitionTime="800" open={countSecond === 3} onTriggerOpening={() => setCountSecond(3)}>
                        <p>Scarica l'App</p>
                        </Collapsible>
                    </li>
                    <li>
                    <Collapsible 
                            trigger="Autobus +" 
                            triggerWhenOpen="Autobus -" 
                            transitionTime="800" open={countSecond === 4} onTriggerOpening={() => setCountSecond(4)}>
                        <p>Tper</p>
                        </Collapsible>
                    </li>
                </ul>
            </div>
        </div>
        <a className={styles.link} href="/">Link per download mappa</a>
    </>
}

export default InfoLocation