import { useLocation } from 'react-router-dom';
import styles from '../../css/Header.module.css';
import { isBrowser } from 'react-device-detect';
import { motion, useScroll, useTransform } from "framer-motion";
import { useRef } from 'react';

const Header = (props) => {

    const ref = useRef(null)
    const { scrollYProgress } = useScroll({
    target: ref,
    offset: ["center end", "end end"]
    })

    const translateY = useTransform(scrollYProgress, [0, 1], [-2500, 0]);
    const scale = useTransform(scrollYProgress, [0.8, 1], [0, 1]);
    const opacity = useTransform(scrollYProgress, [0, 1], [0, 1]);

    let url = useLocation();

    return <header ref={ref}>
        <div className={styles.banner} style={{background: `url("${props.img}") center center / cover no-repeat`}}>
            {isBrowser && url.pathname === '/' | url.pathname === '/home' ? <a href="/" _blank className={styles.button}>Virtual Tour</a> : ''}
        </div>
        <motion.h1 style={{ translateY,scale, opacity }} transition={{
          type: 'spring',
          stiffness: 50, // Adjust stiffness to control speed
          damping: 10, // Adjust damping to control smoothness
          duration: 1 // Adjust duration of transition
        }}>{props.title}</motion.h1>
    </header>
}

export default Header;